ul.timeline {
    list-style-type: none;
    position: relative;
}
ul.timeline:before {
    content: ' ';
    background: #FD9D01;
    display: inline-block;
    position: absolute;
    left: 30px;
    width: 8px;
    height: 100%;
    z-index: 400;
    margin-top: 20px;
}
ul.timeline > li:before {
    content: ' ';
    background: #FD9D01;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 3px solid #FD9D01;
    left: 20px;
    width: 28px;
    height: 28px;
    z-index: 400;
    margin-top: 20px;
}
ul.timeline1 {
  list-style-type: none;
  position: relative;
  margin-top: -20px;
}
ul.timeline1:before {
  content: ' ';
  background: #E5E5E5;
  display: inline-block;
  position: absolute;
  left: 30px;
  width: 8px;
  height: 100%;
  z-index: 400;
  margin-top: 20px;
}
ul.timeline > li , ul.timeline1 > li {
  margin: 20px 0;
}
ul.timeline1 > li:before {
  content: ' ';
  background: #E5E5E5;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 3px solid #E5E5E5;
  left: 20px;
  width: 28px;
  height: 28px;
  z-index: 400;
  margin-top: 20px;
}
